*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.main{
    display: flex;
    box-sizing: border-box;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background-color: white;
}
.App{
    padding: 10px;
    width: 25em;
    height: 35em;
    border: 2px solid black;
    background: black;
    color: white;
    border-radius: 10px;
}
.search input{
    width: 100%;
    height: 100px;
    text-align: right;
    font-size: 65px;
    border: none;
    /* background: rgb(65, 96, 233); */
    color: black;
    border-radius: 10px;
}
.butn{
    /* border: 2px solid; */
    box-sizing: border-box;
    border-radius: 10px;
    margin-top: 25px;
    /* margin-right: 0; */
    height: 25.5rem;
}
.button{
    display: grid;
    grid-template-columns: auto auto auto auto;
    text-align: center;
    justify-content: center;
}
.button button{
    /* display: none; */
    border: none;
    /* border-radius: 18px; */
    font-size: 25px;
    height: 5.06rem;
    width: 5.78rem;
    /* background: rgb(104, 99, 99); */
    cursor: pointer;
}
.clear{
    background: rgb(122, 144, 243);
}
.ac{
    border-radius: 10px 0 0 0;
    background-color: tomato;
}
.del{
    background-color: tomato;
}
.color{
    background-color: rgb(122, 144, 243);
}
#zero{
    grid-row: 5;
    grid-column: 1 / 3;
    width: 11.59rem;
    border-radius: 0 0 0 10px;
}
.eq{
    border-radius: 0 0 10px 0;
    background-color: yellowgreen;
}
.div{
    border-radius: 0 10px 0 0;
}